$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-home {
  #homeIntro {
    .homeIntroInner {
      color: #fff;
      .homeIntroTag {
        margin-bottom: 2rem;
      }
      .button.white {
        margin-right: 1rem;
      }
    }
  }
  #homeWelcome {
    .ulp-welcome-logo {
      width: 186px;
      display: block;
      margin: auto;
    }
  }
  #homeFeatureThree {
    .homeFeatureContainer {
      background-color: $blue;
      background-size: cover;
      background-position: center;
      border-radius: 5px;
      color: #fff;
      padding: 2rem;
      .homeFeatureIconContainer {
        border-radius: 50%;
        width: 100px;
        height: 100px;
        margin: auto;
        overflow: hidden;
        background: rgba(255,255,255,0.2);
        i {
          font-size: 2rem;
        }
      }
      .button.cta {
        color: $blue;
        &:hover {
          color: $yellow;
        }
      }
    }
  }
  #homeNews {
    background: #f2f2f2;
    color: $blue;
    .headerContainer {
      position: relative;
      i {
        position: absolute;
        top: 5px;
        right: 20px;
        color: #dadada;
        font-size: 1.75rem;
        &.fa-thumbtack {
          transform: rotate(45deg);
        }
      }
    }
    .homeNewsPostContainer {
      margin-bottom: 15px;
      padding: 15px;
      background: #fff;
      border-radius: 5px;
      border: 1px solid #ddd;
      h3 {
        font-size: 1.5rem;
      }
      .postMeta {
        color: #A3A3A3;
        font-size: 95%;
      }
      .homeNewsThumbContainer {
        background-color: #333;
        position: relative;
        height: 200px;
        background-size: cover;
        background-position: center;
        border-radius: 5px;
        .homeNewsThumbLink {
          position: absolute;
          top: 0; right: 0; bottom: 0; left: 0;
          z-index: 2;
        }
        .homeNewsThumbDate {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          background: $yellow;
          color: $white;
          text-align: center;
          font-size: 0.75em;
          padding: 5px;
        }
      }
      h3 {
        a {
          color: $blue;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
    .upFeedItem {
      margin-top: 15px;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      h4 {
        a {
          color: $blue;
          &:hover, &:visited {
            color: $blue;
            text-decoration: none;
          }
        }
      }
      .newEventImage {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        background-size: cover;
        background-position: center;
        .newEventImageLink {
          position: absolute;
          top: 0; bottom: 0; right: 0; left: 0;
        }
      }
      .newEventText {
        padding: 15px;
      }
      p {
        margin-bottom: 0.25rem;
        a { display: inline-block; }
      }
      .postTime {
        font-style: italic;
      }
      .postReadMore a {
        font-weight: bold;
        color: $yellow;
        margin-bottom: 0;
      }
      .pastEventContainer {
        position: relative;
        overflow: hidden;
        background-color: #f2f2f2;
        border-radius: 5px;
        margin-bottom: 15px;
        padding-top: 10rem;
        background-size: cover;
        background-position: center;
        box-shadow: none;
        transition: all .2s ease-in-out;
        &:hover {
          box-shadow: 0 0 25px -5px #000;
        }
        h3 {
          font-size: 1rem;
        }
        .pastEventTeaser {
          position: absolute;
          left: 0;
          bottom: 0;
          right: 0;
          padding: 1rem;
          background: $blue;
          color: $white;
        }
        .pastEventDate {
          text-transform: uppercase;
          color: $yellow;
          font-size: 0.75rem;
          margin-bottom: 0;
        }
      }
    }
    #homeTestimonials {
      .homeTestimonialsContainer {
        background: #fff;
        border-radius: 5px;
        border: 1px solid #ddd;
        padding: 2rem;
        min-height: 400px;
        .homeTestiSlider {
          width: 100%;
        }
        .homeTestimonialsContainerInner {
          .homeTestimonialsImgWrap {
            width: 200px;
            height: 200px;
            border-radius: 50%;
            border: 2px solid $yellow;
            display: inline-block;
            margin: auto auto 2rem auto;
          }
          .homeTestimonialsImg {
            border: 2px solid $white;
            overflow: hidden;
            background-position: center;
            background-size: cover;
            width: 100%;
            height: 225px;
          }
          .homeTestAuthorTitle {
            color: $yellow;
          }
        }
      }
    }
  }
  #homeCTA {
    background-color: $blue;
    background-size: cover;
    background-position: center;
    color: $white;
    h2 {
      margin: 0;
    }
    i {
      font-size: 2rem;
    }
  }
  #homeStats {
    .facilityNumContainer {
      min-height: 170px;
      .facilityNumInner {
        position: relative;
        min-height: 140px;
        .facilityNumBig {
          position: absolute;
          z-index: 0;
          top: 0;
          left: 0;
          font-size: 8rem;
          font-weight: bold;
          color: #f2f2f2;
        }
        p {
          position: absolute;
          display: inline-block;
          z-index: 1;
          bottom: 0;
          right: 0;
        }
      }
    }
  }
  #homeSponsors {
    h2 span {
      color: $yellow;
    }
    .homeSponsorsContainer {
      width: 850px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      img {
        margin-bottom: 2rem;
      }
      h4 {
        margin-top: 2rem;
      }
    }
  }
}